import React, {Components} from 'react';

const TITLES =[
    "a software developer",
    "a music enthusiast",
    "an adventurer",
    "a human"
];

class Title extends Components{
    state = {titleIndex: 0};

    render(){
        const title  = TITLES[this.state.titleIndex];

        return(
            <p>I am {title}</p>
        )
    }
}

export default Title;